import React from 'react'
import { l } from '../l'

export default Levels = props => {
  return (
    <>
      <div className="title"><NavLink to="/plugins" className="back"><img src="/static/img/arrow/black_left.png" /></NavLink><img src={`/static/img/plugins/${props.title}.png`} />{l['plugin_' + props.title]}</div>
      <div className="commands">
        {props.state.commands &&
          Object.entries(props.state.commands).map(([commandName, command], i) => <Command key={i} status={command.enabled} click={n => props.updateState({...props.state, commands: {...props.state.commands, ...{[commandName]: {...command, ...{enabled: n}}}}})}>!{commandName}</Command>)}
      </div>
    </>
  )
}